@mixin shadow{
    box-shadow: 0px 0px 20px -12px rgba(0, 0, 0, 0.8);
}

@mixin flexCenter{
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin phone {
  @media (max-width: 900px) {
    @content;
  }
}

@mixin centeredBg($src){
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    background-image: url("../images/#{$src}");
    @include phone{
        background-image: url('../images/mob.jpg');
        background-size: 100vw auto;
    }
}

@mixin centeredBgNoMob($src){
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    background-image: url("../images/#{$src}");
}

@mixin desktop {
  @media (min-width: 900px) {
    @content;
  }
}

.animated-svg{
    z-index: -10;
    transition: .3s all ease-out;
    @include phone{
        display: none;
    }
}
*{
    box-sizing: border-box;
}