.benefitsHeading{
    width: 250px;
    position: relative;
    img{
        position: absolute;
        width: 400px;
        object-fit: contain;
        z-index: 2;
        top: 0;
        left: 0;
    }
    .headings-cont{
        margin: auto;
        padding: 50px;
        padding-top: 30px;
        h3{
            color: white;
            z-index: 3;
            position: relative;
            font-size: 15px;
            min-width: 250px;
        }
         h2{
            margin: 0;
            position: relative;
             z-index: 3;
            font-size: 30px;
            color:white;
            padding-bottom: 0;
             margin-bottom: 20px;
    }
        h2.solo{
            margin-top: 20px;
            margin-bottom: 0;
        }
    }}

.benefitsGraphic{
    position: relative;
    margin-top: 50px;
    width: 400px;
}

.benefitsGraphic img{
    object-fit: contain;
     position: absolute;
}

.benefitsGraphic> img:nth-child(1){
    top:10px; 
    width: 350px;
}

.benefitsGraphic> img:nth-child(2){
    top:90px;
    left: -40px;
    width: 340px;
}

.benefitsCont{
    position: relative;
    min-height: 100vh;
    @include phone{
        background-size: auto 100%;
    }
}

.benefitsBoxCont{
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
    @include phone{
        flex-direction: column;
        justify-content: flex-start;
    }
    min-height: 560px;
    flex-wrap: wrap;
    
}

.benefitsBox{
    width: 34%;
    z-index: 20;
    position: relative;
    height: fit-content;
    min-height: 200px;
    background: rgba(255,255,255,0.5);
    padding: 30px;
    display: flex;
    margin: 0 40px;
    @include phone{
        min-height: 0;
        margin: 0;
        width: 100%;
    }
    border-radius: 20px;
    color: black;
        ion-icon{
            color: white;
            max-height: 40px;
            max-width: 40px;
            min-height: 40px;
            min-width: 40px;
            border-radius: 50%;
            background: $darkblue;
            margin-top: 20px;
            padding: 20px;
            margin-right: 20px;
        }
    p{
        line-height: 25px;
    }
}

.benefitsBoxHeader{
    display: flex;
}

.benefitsBoxHeader img{
    width: 80px;
    display: block;
    object-fit: contain;
}

.benefitsBoxHeader h2{
    margin-left: 20px;
    color: $darkblue;
}

.circleCont{
    position: absolute;
    width: 100%;
    height: 100%;
    @include flexCenter();
}

.circle{
    max-height: 400px;
    object-fit: contain;
}
