.referencesScreen {
    min-width: 100vw !important;
     @include centeredBg('4.jpg');
    max-width: 100vw;
    color: white;

}

.references-svg{
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    opacity: 1;
    &.pre{
        bottom: -40vh;
        opacity: 0;
    }
}

.referencesMainCont{
    min-height: 100vh;
    @include centeredBg('4.jpg');
}

.referencesChooser{
    margin-top: 130px;
    width: 75%;
    display:flex;
    flex-wrap: wrap;
}

.chooserButton{
    color: black;
    border: 2px solid $lightblue;
    cursor: pointer;
    margin-bottom: 10px;
    margin-right: 20px;
    outline: none;
    padding: 13px;
    transition: $short;
    font-weight: bold;
    border-radius: 7px;
    font-size: 15px;
    background: #9e9e9e;
}

.chooserButton:hover{
    background: #EDEFFE !important;
}

.referencesWrapper{
    width: 90%;
    @include flexCenter();
    flex-wrap: wrap;
}

.referencesCont{
    @include flexCenter();
    @include shadow();
    position: relative;
    flex-direction: column;
    margin:15px;
    cursor: pointer;
    height: 200px;
    width: 350px;
    text-align: center;
    background: white;
    color: black;
    border-radius: 8px;
    font-weight: bold;
    transition: $short;
    &:hover{
        transform: scale(1.05)
    }
    p{
        width: 80%;
        color: $lightblue;
        padding-bottom: 40px;
        margin: 5px;
    }
    img{
        width: 50%;
        max-height: 70px;
        object-fit: contain;
    }
    div{
        width: 100%;
        height: 40px;
        position: absolute;
        bottom: 0;
        line-height: 35px;
        color: white;
        background: $darkblue;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
    }
}

.references.content, .research.content{
    z-index: 1;
    position: relative;
    background: $darkblue;
}

.fade{
    position: absolute;
    background: linear-gradient(180deg, rgba(33,33,33,1) 0%, rgba(43,43,43,1) 52%, rgba(80,80,80,0) 100%);
    width: 100%;
    height: 60px;
    top: 0px;
}

.references.content > h2, .reasearch.content > h2{
    margin-top: 0;
    padding-top: 70px;
    font-size: 30px;
    text-align: center;
}

.mainCont{
    font-size: 17px;
    display: flex;
    padding:50px;
    .mainInfo{
        background: white;
        margin-right: 40px;
        border-radius: 15px;
        flex: 2;
        color: black;
        padding: 20px;
        @include shadow();
    }
    .bonusInfo{
        font-size: 15px;
        flex: 1;
        div{
            margin-bottom: 40px;
            background: white;
            border-radius: 15px;
            color: black;
            padding: 20px;
            img{
                width: 30%;
                object-fit: contain;
            }
            .logo2{
                width: 70%;
            }
        }
        @include phone{
            width: 94%;
            margin: 20px auto;
            padding: 20px 5px;
        }
    }
    @include phone{
        flex-direction: column;
        align-items: center;
        padding: 0;
    }
}

.tags{
    .tag-cont{
        padding: 0 !important;
        margin-bottom: 0 !important;
        .tag-symbol{
            color: $darkblue;
        }
        a{
        color: black;
            &:hover{
                color: $lightblue;
            }
        }
    }
}

@include phone{
    .research.refImg {
        width: 50% !important;
        margin-bottom: 20px;
    }
    .opvai{
        padding: 0 !important;
    }
} 

.bonusInfo > div > div > img{
    width: 80px;
    object-fit: contain;
}

.bonusInfo > div > .imgCont{
    display: flex;
    padding-bottom: 0;
    margin-bottom: 5px;
}

.mainCont > .mainInfo > h2{
    margin-top: 0;
    color: #0B4270;
    width: fit-content;
    padding: 5px;
    position: relative
}

.mainCont > .mainInfo > h2::after{
    content: '';
    height: 3px;
    width: 85%;
    position: absolute;
    bottom: 0;
    left: 5px;;
    background: black;
}

@include phone{
    .mainCont > .mainInfo{
        width: 86%;
        margin-right: 0;
    }
    .mainCont > .bonusInfo{
        width: 100%;
    }
}

.refDecor{
    position: absolute;
    width: 100%;
    object-fit: contain;
    z-index: -1;
}

.refDecor.one{
    top: 0;
}

.refDecor.two{
    bottom: 0;
}

.references.refImg{
    width: 95%;
    border-radius: 15px;
    object-fit: cover;
    max-height: 350px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.research.refImg{
    width: 50%;
    object-fit: contain;
}

.bonusInfo > div > h3{
    color: #0B4270;
}

.sliderChooserCont{
    background: $darkblue;
    border-radius: 20px;
    @include flexCenter();
    .arrowCont{
        img{
            width: 20px;
            object-fit: contain;
            filter: invert(1)
        }
    }
}

.sliderChooser{
    
    min-height: 70px;
    width: 166%;
    margin-left: -33%;
    color: white;
    text-align: center;
    @include flexCenter();
    .item{
        width: 25%;
        @include flexCenter();
        transition: $short;
        padding: 10px;
        font-size: 15px;
    }
    .item.active{
        color: $lightblue;
        transform: scale(1.2);
        font-weight: bold;
    }
}

.swiper-container {
width: 100%;
/*this gives the dimension of slider container*/
/*these values will make the container as big as the parent div*/
    min-height: 480px;
/*important parameter to not disrupt the container when slides increases in coverflow and other effect*/
}
.swiper-slide {
width:100% !important;
/*this one holds the dimension of each slides and can be changed according to the user wish*/
}

.swiperCont{
    @include flexCenter();
    flex-wrap: wrap;
}

.swiper-pagination-bullets{
    bottom: 10px !important;
}

.swiper-pagination-bullet{
    width: 12px !important;
    height: 12px !important;
    border: 1px solid white;
    box-sizing: border-box;
}

//new

.references-cont{
    @include centeredBg('21.jpg');
    background-size: 100% auto;
    position: relative;
    h1{
        margin-top: 0;
        padding-top: 50px;
        font-size: 33px;
        text-align: center;
        color: black;
        @include phone{
            max-width: 90%;
            margin: auto;
            margin-bottom: 10px;
        }
    }
    .back-svg{
        position: absolute;
        bottom: 0;
        z-index: 1;
        left: 0;
        min-width: 100vw;
        max-width: 100vw;
        @media (max-width:1400px){
            min-width: 0;
            max-width: 1000%;
            height: 70%;
        }
        @include phone{
            height: 50%;
        }
    }
    .references-content{
        min-height: 600px;
        display: flex;
        @media(max-width:1400px){
            flex-direction: column-reverse;
            align-items: center;
        }
        h3{
            font-size: 23px !important;
        }
        .bonus-info{
            align-self: flex-end;
            @media(max-width:1400px){
                align-self: auto;
                margin-right: auto;
            }
            padding-bottom: 150px;
            display: flex;
            flex: 1;
            flex-direction: column;
          .info-cont{
            position: relative;
            margin-top: 50px;
              max-width: 60%;
            margin-left: 40px;
            z-index: 4;
                  .tag-cont{
                      a{
                          color: white !important;
                          &:hover{
                              text-decoration: underline;
                              color: $darkblue !important;
                          }
                      }
                  }
              
        }  
        }
        .main-info{
            height: fit-content;
            z-index: 2;
            color: black;
            font-size: 16px;
            line-height: 24px;
            flex: 2;
            max-width: 900px;
            min-width: 600px;
            padding: 30px 20px;
            margin: 100px 120px 100px 0;
            @include shadow();
            background: white;
            @media(max-width:1400px){
                min-width: 0;
                width: 92%;
                margin: 20px;
            }
            .client-cont{
                background: white;
                color: black;
                display: flex;
                flex-direction: row;
                img{
                    margin-left: -10px;
                    margin: auto;
                    display: block;
                    width: 300px;
                    object-fit: contain;
                    @include phone{
                        width: 50%;
                        max-width: 250px;
                        margin-left: auto;
                    }
                }
                h3{
                    margin: 0;
                    padding: 0;
                }
            }
        }
    }
}














