html,
body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
     user-select: none;
}

#root{
    overflow-x: hidden;
    position: relative;
}

@import './variables';
@import './mixins';
@import './menu';
@import './home';
@import './research';
@import './services';
@import './benefits';
@import './contact';
@import './references';
@import './products';


.sliderCont {
    position: relative;
    height: 100vh;
    width: 300%;
    display: flex;
    margin-left: -100%;
    transition: $long;

}


.placeholderScreen{
    @include centeredBg('bg6.jpg');
    height: 100vh;
    width: 100%;
}

.blankScreen{
    height: 0;
    width: 100%;
}


.blackScreen {
    height: 100vh;
    width: 100%;
    background: black;
}

.blueScreen {
    height: 100vh;
    width: 100%;
    background: blue;
}

.greenScreen {
    height: 100vh;
    width: 100%;
    background: green;
}

.redScreen {
    height: 100vh;
    width: 100%;
    background: red;
}

.video{
    position: fixed;
    height: 100vh;
    width: 100vw;
    object-fit: cover;
    opacity: 1;
}

.body-cont{
    margin: 0;
    transition: $long;
}
