ul{
    list-style: none;
}

a{
    text-decoration: none;
    cursor: pointer;
    color: black;
}

label {
    -webkit-tap-highlight-color: transparent;
}

section{
    height: 100vh;
}

nav {
    background: transparent;
    z-index: 16;
    margin: 0;
    position: absolute;
    width: 100%;
    transition: $long;
    top: 0;
    padding: 10px 0;
}


.checkbtn {
    font-size: 30px;
    color: black;
    float: right;
    line-height: 60px;
    margin-right: 40px;
    cursor: pointer;
    display: none;
}

#check {
    display: none;
}

.menu {
    width: 100%;
    font-weight: lighter;
    padding: 0;
    position: relative;
    padding-right: 230px;
    list-style: none;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    a {
        text-decoration: none;
        color: black;
        padding: 0 20px;
        font-size: 22px;
        font-weight: bold;
    }
}


.logo {
    margin-top: 10px;
    margin-left: -70px;
    position: absolute;
    left: 200px;
    height: 100px;
    color: white;
    font-size: 35px;
    line-height: 80px;
    padding: 0 100px 0 0;
    font-weight: bold;
}

.slideUpButton{
    position: fixed;
    outline: none;
    bottom: 30px;
    right: 40px;
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 50%;
    background: $lightblue;
    color:white;
    z-index: 20;
    transition: $short;
    &:hover{
        background-color: grey;
    }
}

.menuitem {
    margin-left: auto;
    display: inline-block;
    padding: 30px 10px;
    color: black;
    transition: $long;
    &:hover {
    color: #A3A7CD !important;
    }
}

.menu_right {
    margin-right: 10%;
}


.menuclick {
    cursor: pointer;
}


.logo-mobile {
    display: none;
    height: 60px;
    margin-left: 30px;
    margin-top: 5px;
}

.logoMobileLink{
    width: 80px;
}


label.checkbtn {
    height: 60px;
    margin-left: 50px;
}

.menubars{
    position: relative;
    margin-right: 18px;
    margin-top: 13px;
    span{
        z-index: 50;
        display: block;
        position: absolute;
        height: 6px;
        width: 40px;
        background: black;
        border-radius: 9px;
        opacity: 1;
        transform: rotate(0deg);
        transition: $short;
    }
    span:nth-child(1) {
      top: 0px;
    }
    span:nth-child(2),span:nth-child(3){
        top: 12px;
    }
    span:nth-child(4) {
      top: 24px;
    }
}


#check:checked ~ label > .menubars span:nth-child(1) {
  top: 12px;
  width: 0%;
  left: 50%;
}

#check:checked ~ label > .menubars span:nth-child(2) {
  transform: rotate(45deg);
}

#check:checked ~ label > .menubars span:nth-child(3) {
  transform: rotate(-45deg);
}

#check:checked ~ label > .menubars span:nth-child(4) {
  top: 12px;
  width: 0%;
  left: 50%;
}

@media (max-width : 1400px) {
    nav {
        height: 60px;
    }

    .logo {
        display: none;
    }

    .logo-mobile {
        display: inline;
    }

    .menuitem a {
        font-size: 16px;
    }

    .checkbtn {
        display: block;
    }

    .menu {
        margin: 0;
        position: fixed;
        width: 30%;
        height: 100vh;
        float: right;
        display: block;
        z-index: 5;
        background: #eef0ff;
        border-left: 2px solid black;
        top: 0;
        left: 100%;
        padding-right: 0;
        text-align: center;
        transition: $long;
        padding-top: 50px;
    }

    .menuitem {
        display: block;
        margin: 0;
        line-height: 0;
        font-size: 20px;
    }

    .menu a:hover,
    .menu a.active {
        background: none;
        color: #603813;
        transition: $long;
    }

    #check:checked ~ .menu {
        left: 70%;
    }
}

@media(max-width:1100px){
    .menu{
        width: 40%;
    }
    #check:checked ~ .menu {
        left: 60%;
    }
}
@include phone{
    .menu{
        width: 60%;
    }
    #check:checked ~ .menu {
        left: 40%;
    }
}
@media(max-width:700px){
    .menu{
        width: 80%;
    }
    #check:checked ~ .menu {
        left: 20%;
    }
}
