
textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
  -webkit-appearance: none;
  border-radius: 0;
}

a{
    transition: $short;
}

a:hover{
    color: $lightblue;
}

.contactScreen {
    height: 100vh;
    width: 100%;
    @include centeredBg('6.jpg');
    color: white;
    @include flexCenter();
    align-items: center;
}

.contact-svg{
    width: 120vw;
    position: fixed;
    bottom: 0;
    left: 0;
    opacity: 1;
    &.pre{
        bottom: -40vh;
        opacity: 0;
    }
}

@include phone{
    .contactScreen{
        height: fit-content;
        align-items: center;
        flex-direction: column;
    } 
}

.contactLower{
    border-radius: 15px;
    position: relative;
    height: 500px;
    margin-top: 60px;
    background: white;
    @include shadow();
    @include phone{
        width: 90%;
        height: auto;
        margin-top: 90px;
        }
    }

        

.contactUpper{
    position: absolute;
    top:50px;
    border-radius: 15px;
    left: -50px;
    height: 400px;
    width: 340px;
    background: $darkblue;
    @include shadow();
    @include phone{
        display: none;
    }
    div{
        div{
            a{
                color: white;
            }
        }
    }
}

.contactUpperMobile{
    display: none;
    position: relative;
    margin-top: 50px;
    padding-top: 10px;
    border-radius: 15px;
    height: 400px;
    width: 90%;
    margin-bottom: 20px;
    background: $darkblue;
    @include shadow();
    @include phone{
       display: block; 
        div{
            div{
                a{
                    color: white !important;
                    transition: $short;
                    &:hover{
                        color: $lightblue !important;
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}



.contactUpper > h3, .contactUpperMobile > h3{
    margin-left: 15px;
}

.contactUpper > div, .contactUpperMobile > div{
    display:flex;
    margin: 10px;
    margin-left: 15px;
}

.contactUpper > div > i, .contactUpperMobile > div > i{
    margin-right: 15px;
}

form{
    box-sizing: border-box;
}

.contactForm{
    margin-left: 350px;
    @include phone{
        margin-left: 0;
    }
    padding-right: 60px;
    width: 450px;
    display: flex;
    flex-direction: column;
    color:black;
    padding-bottom: 50px;
    @include phone{
        padding-left:20px;
        width: 90%;
    }
    h3{
        padding-top: 20px;
        font-size: 24px;
        color: $darkblue;
    }
    p {
        margin-top: 0;
        font-size: 12px;
        color: black;
        opacity: 0.6;
    }
    input{
        margin-bottom: 10px;
        background: #e4e7ec;
        width: 90%;
        border:none;
        outline:none;
        border-radius: 10px;
        padding: 14px;
        display: block;
        @include phone{
            width: 120%;
        }
    }
    textarea{
        width: 90%;
        font-family: 'Roboto', sans-serif;
        padding:14px;
        height: 100px;
        resize: none;
        border:none;
        outline: none;
        border-radius: 10px;
        background: #e4e7ec;
        margin-bottom: 20px;
        @include phone{
            width: 120%;
        }
    }
}

.submitButton{
    background: $lightblue !important;
    border-radius: 20px !important;
    width: 140px !important;
    color: white;
    transition: $short;
    @include shadow();
    &:hover{
        background: grey !important;
    }
}


.extraInfo{
    font-size: 10px;
    opacity: 0.6;
    display: flex;
    position: absolute;
    bottom: 10px;
   justify-content: flex-end;
    flex-direction: column;
}
