.researchScreen {
    width: 100%;
    color: white;
    position: relative;
}

.researchCont{
    min-height: 100vh;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
         @include centeredBg('5.jpg');
    .servicesBox{
        margin: 30px;
        width: 400px;
        height: 400px;
    }
}


@include phone{
    .researchCont{
        padding-top: 100px;
        flex-direction: column;
    }
    .researchCont > .servicesBox{
        width: 85%;
    }
}

.researchCont > .servicesBox > h2{
    max-width: 85%;
}

.researchCont > .servicesBox > h3{
    margin-top: 0;
}

.bioMedires{
    min-height: 100vh;
    background: #494947;
}

.logos{
    @include flexCenter();
}

.info-cont.res{
    @include phone{
        margin-left: 0;
    }
}

.logos > img{
    width: 17%;
    object-fit: cover;
    margin: 10px;
    @include shadow();
}

.researchGarants{
    padding: 30px;
    @include phone{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 0 !important;
        padding-top: 20px !important;
    }
}

.opvai{
    padding: 0 80px 0 0;
    box-sizing: border-box;
}

.head-icon{
    position: absolute;
    top: 10%;
    right: 10%;
    object-fit: contain;
    width: 70%;
    max-width: 600px;
    max-height: 700px;
    @include phone{
        display:none;
    }
}
