body{
 
}

.homeScreen {
    width: 100%;
    min-height: 100vh;
    position: relative;
    display: flex;
    @include centeredBgNoMob('1.jpg')
    div{
        flex: 1;
    }
    .icon-cont{
        @include flexCenter()
    }

}

.main-heading{
    padding-top: 180px;
    margin-left: 140px;
    font-size: 40px;
    text-transform: uppercase;
    .what-we-do{
        font-size: 22px;
        max-width: 500px;
        margin-top: 20px;
        line-height: 32px;
        display: block;
        text-transform: none;
        color: rgba(0,0,0,0.4);
        @include phone{
            width: 90%;
            display: block;
        }
    }
}

    .home-icon{
        object-fit: contain;
        width: 80%;
        max-height: 600px;
        margin-top: 70px;
        height: 80%;
        z-index: 0;
        min-width: 400px;
        @include phone{
            width: 50%;
             bottom: 20px;
            right: 20px;
            position: absolute;
            height: auto;
            margin-top: -20px;
            margin-left: auto;
            margin-right: 15px;
        }
                @media(max-width:600px){
            width: 50%;
             bottom: -3%;
            right: -30%;
            position: absolute;
            height: auto;
            margin-top: -20px;
            margin-left: auto;
            margin-right: 15px;
            margin-top: 30px;
            width: 60%;
        }
/*        @media (max-width:500px){
            width: 30%;
        }*/
    }


.moreInfo {
    font-size: 20px;
    margin-left: 140px;
    margin-top: 30px;
    padding: 12px 30px;
    border-radius: 30px;
    outline: none;
    border: none;
    position: relative;
    background: $darkblue;
    color: white;
    cursor: pointer;
    transition: $short;
    @include shadow();
}

@include phone{
    .main-heading {
        font-size: 30px;
        padding-top: 100px;
        margin-left: 20px;
    }
    .moreInfo{
        margin-left: 20px;
        margin-top: 10px;
    }
}

.slideHvr:hover::before {
    opacity: 1;
    width: 100%;
}

.slideHvr::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    z-index: 1;
    border-radius: 29px;
    opacity: 0;
    background-color: rgba(255,255,255,0.2);
    transition: $short;
}